import React, { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import UserContext from "./UserContext";
import styles from "./SignUp.module.css";
import loginStyles from "./Login.module.css";

const SignUp = () => {
  const { users, dispatch } = useContext(UserContext);
  const navigate = useNavigate();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [signupError, setSignupError] = useState(null);
  const [usernameValid, setUsernameValid] = useState(false);
  const [passwordValid, setPasswordValid] = useState(false);
  const [usernameChecked, setUsernameChecked] = useState(false); //    
  const [usernameCheckMessage, setUsernameCheckMessage] = useState(null); //      

  const handleUsername = (e) => {
    setUsername(e.target.value);
    const regex = /^[a-zA-Z0-9]+$/;
    setUsernameValid(e.target.value.length >= 4 && regex.test(e.target.value));
    setUsernameChecked(false); //   usernameChecked false 
  };

  const handlePassword = (e) => {
    setPassword(e.target.value);
    const regex =
      /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/;
    setPasswordValid(regex.test(e.target.value));
  };

  const [passwordMatch, setPasswordMatch] = useState(true);

  const handleConfirmPassword = (e) => {
    setConfirmPassword(e.target.value);
    setPasswordMatch(e.target.value === password);
  };

  const handleUsernameCheck = () => {
    //    
    const existingUser = users.find((user) => user.username === username);
    if (existingUser) {
      setUsernameCheckMessage(null);
      alert("  ."); //  
    } else {
      setUsernameChecked(true);
      setSignupError(null); //    signupError null 
      alert("  ."); //  
    }
  };

  const handleSignUp = () => {
    // , ,       
    if (!username || !password || !confirmPassword) {
      alert("    .");
      return;
    }

    //     
    if (!usernameChecked) {
      alert("  .");
      return;
    }

    //     
    if (password !== confirmPassword) {
      alert("    .");
      return;
    }

    //   ,        
    const newUser = { username, password };
    dispatch({ type: "SIGN_UP", payload: newUser });
    navigate("/");
  };

  const handleCancel = () => {
    navigate("/");
  };

  return (
    <div className={styles.page}>
      <header className={styles.header}>
        <h2 className={styles.signUptitle}></h2>
      </header>
      <div className={styles.InputWrapper}>
        <label>
          <div className={styles.signupList}></div>
          <div className={styles.idBox}>
            <input
              type="text"
              placeholder=",  4 "
              value={username}
              onChange={handleUsername}
              className={loginStyles.input}
            />
            <button onClick={handleUsernameCheck} className={styles.idChk}>
               
            </button>
          </div>
          {!usernameValid && username.length > 0 && (
            <div className={styles.errorMessageWrap}>
                .
            </div>
          )}
          {usernameCheckMessage && (
            <div className={styles.successMessageWrap}>
              {usernameCheckMessage}
            </div>
          )}
        </label>
        <br />
        <label>
          <div className={styles.signupList}></div>
          <input
            type="password"
            placeholder=", ,   8~20"
            value={password}
            onChange={handlePassword}
            className={loginStyles.input}
          />
          {!passwordValid && password.length > 0 && (
            <div className={styles.errorMessageWrap}>
                .
            </div>
          )}
        </label>
        <br />
        <label>
          <input
            type="password"
            value={confirmPassword}
            placeholder=" "
            onChange={handleConfirmPassword}
            className={loginStyles.input}
            style={{ marginTop: 22 }}
          />
          {!passwordMatch && (
            <div className={styles.errorMessageWrap}>
                .
            </div>
          )}
        </label>
      </div>

      <br />
      <div className={styles.signupBtnWrapper}>
        <button onClick={handleSignUp} className={styles.signupBtn}>
          
        </button>
        <button onClick={handleCancel} className={styles.signupBtn}>
          
        </button>
      </div>
      {signupError && <div className={styles.signUpErrMsg}>{signupError}</div>}
    </div>
  );
};

export default SignUp;
